.profile__rounded{
  width: 25vw;
  border-radius: 50%;
}

.education__text-center{
  text-align: center;
}

.education__text-left{
  text-align: left;
}

.icon__size{
  font-size: 6em;
}

.icon__size-small{
  font-size: 3em;
  margin-right: .25em;
}



/*global styles*/
ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a.nostyle:link {
  text-decoration: inherit;
  color: inherit;
  cursor: auto;
}

a.nostyle:visited {
  text-decoration: inherit;
  color: inherit;
  cursor: auto;
}

*{
  scroll-behavior: smooth;
  font-family: 'Open Sans', sans-serif;
}


/*header*/
.fullheighttest{
  height: 100vh;
}

.tm-section-texture{
  background: rgb(213,242,255);
  background: url("assets/section-background.svg") 50% 17vh no-repeat, linear-gradient(to left top, #28a5f5, #1e87f0) 0 0 no-repeat;
  color: white;
}

.header__button{
  background: lightsalmon;
}

.header__text-sub{
  color: ivory;
}

.header__text-main{
  font-size: 8vh;
  color: ivory;
}